body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(159, 179, 216);
  background-image: none;
  background-size:cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    display: flex;
    justify-content: center;
}

.main-div-container{
    width: 180vh;
    height: 80vh;

    color: black;
}
.div-weather-now{
    display: flex;
    justify-content: center;
    margin-top: 5%;
}
.div-weather-future{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 3%;
    grid-gap: 5%;
    gap: 5%;
}
.div-logo{
    display: flex;
    justify-content: center;
}
.logo{
    width: 60px;
}
.div-input{
    display: flex;
    justify-content: center;
    margin: 2%;
}
.main-div{
    border-radius: 10%;
    width: 320px;
    display: flex;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.438) 10px 10px 10px 10px;
    background-color: white;
}
.imagem{
    width:160px
}
.div-info{
    text-align: center;
}
.currentStatus{
    font-size: large;
    text-transform:capitalize;
}
.currentTemp{
    font-size: xx-large;
    color: rgb(255, 166, 0);
}
.currentTempMax{
    color: rgb(255, 65, 65);
}
.currentTempMin{
    color: rgb(4, 178, 231);
}
.div-temps{
    justify-content: center;
    display: grid;
    grid-template-columns: 120px;
    grid-template-rows: 50px 50px 50px;
}
.div-min-max{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
